.kv {
	position: relative;
	width: 100vw;
	height: 100vh;
	z-index: 1;
	overflow: hidden;
	margin-bottom: $spacer-sm;
	display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
  @include media-breakpoint-up(md)  {
	 	 margin-bottom: $spacer-xl;
	 }
	 .kv__bg {
		 z-index: 1;
		 object-fit: cover;
	  width: 100%;
	  height: 100%;
	  position: absolute;
	  top: 0;
	  left: 0;
	  right: 0;
	  margin: 0 auto;
	 }
	video {
		z-index: 1;
	//	opacity: 0;
	  object-fit: cover;
	  width: 100%;
	  height: 100%;
	  position: absolute;
	  top: 0;
	  left: 0;
	  right: 0;
	  margin: 0 auto;
	}
	
	
	
	.scroll {
		
		position: absolute;
		bottom: 0;
		z-index: 3;
		margin-bottom: $spacer-md;
		font-size: 14px;
		@include media-breakpoint-up(md)  {
		font-size: 18px;
		}
		&:after {
			animation: scrolldown 3s infinite;
			content: '';
			position: absolute;
			width: 1px;
			background-color: $oldpages;
			left: 0;
			right: 0;
			bottom: 150%;
			margin: 0 auto;
			height: 65px;

		}
	}
	&__details {
		transform: translateY(10px);
		opacity: 0;
		z-index: 3;
		text-align: center;
		position: relative;
		font-family: $serif;
		font-size: 18px;
		padding: $spacer-xs;
		p {
			line-height: 1.4;
			margin: 0;
		}
		@include media-breakpoint-up(md)  {
			font-size: 20px;
			padding: $spacer-sm;
		}
		@include media-breakpoint-up(md)  {
			padding: $spacer-md;
			font-size: 24px;
			p { line-height: 1.6;}
		}
		@include media-breakpoint-up(lg)  {
			position: absolute;
			bottom: 0;
			right: 0;
			text-align: left;
		}
	}
	
	&__beyond {
		opacity: 0;
			padding: $spacer-xs;
			z-index: 10;
			position: absolute;
			top: 60px;
			left: 0;
			text-align: right;
			img {
			width: $spacer-lg;
				@include media-breakpoint-up(md)  {
				width: $spacer-xxl;
				}
		}
		@include media-breakpoint-up(md)  {
			top: 70px;
			padding-right: $spacer-md;
		}

	
	}
	
	
	
}

.kv {
	.kv__text {
		text-align: center;
		position: relative;
		z-index: 2;
		font-family: $serif;
		&__title { 
			width: 70vw;
			max-width: 800px;
			margin: 0 auto;
		}
		&__subtitle {
			
		}
	}
}


.burn_in {
	span { opacity: 0; text-shadow: 0 0 12px #fff; color: transparent;}
}
.chroma-shift {
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.animate-p {
	opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}


@-webkit-keyframes scrolldown {
  0%   { height: 100%; }

  100% { height: 0; }
}
