.list__logo {
	li {
		height: $spacer-lg;
		margin: $spacer-xxs 0;
		display: inline-block;
		a {
			height: 100%;
			display: inline-block;
			transition: all 0.3s ease-in-out;
			opacity: 0.8;
			img {height: 100%;}
			&:hover {
				opacity: 0.5;
				
			}
		}
		
	}
	.list__logo-lg {
			height: $spacer-xxl;
		}
	
	&-startups {
		li {
			margin: $spacer-xxs;
			height: 60px;
			@include media-breakpoint-up(md)  { 
				margin: $spacer-xs;
				height: $spacer-lg;
				
			}
		}
	}
}

.list__nav {
	@include media-breakpoint-up(lg)  { 
	display: flex;
	justify-content: center;
	}
	li {
		list-style-type: none;
		a {
			color: $space;
			text-decoration: none;
			display: inline-block;
			padding: $spacer-xxs;
			font-weight: $reg;
			transition: all 0.3s ease-in-out;
			&:hover {
				color: $evergreen;
				cursor: pointer;
			}
		}
	}
}

.list__logo,
.list__nav {
	padding: 0;
	margin: 0;
}


