body {
	background-color: $space;
	color: $oldpages;

}



.cursor {
	display: none;
}
@media (any-pointer: fine) {
	.cursor {
		z-index: 10;
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		opacity: 0;
	}
	.cursor__inner {
		fill: transparent;
		stroke: $oldpages;
		stroke-width: 1px;
	}
}

.section {
	padding-top: $spacer-lg;
	padding-bottom: $spacer-lg;
	@include media-breakpoint-up(lg) {
		padding-top: $spacer-md;
		padding-bottom: $spacer-md;
	}
	@include media-breakpoint-up(lg) {
		padding-top: $spacer-xl;
		padding-bottom: $spacer-xl;
	}
	&-sm {
		padding-top: $spacer-sm;
		padding-bottom: $spacer-sm;
	}
	
	&.sec-bg-light {
		background-color: $oldpages;
		color: $space;
	}
		&.sec-bg-white {
		background-color: white;
		color: $space;
	}
	&.sec-bg-dark {
		background-color: lighten($space, 10%);
	}
	&.sec-bg-cream {
		background-color: lighten($oldpages, 20%);
		color: $space;
	}
}

.margin-top-lg {
	margin-top: $spacer-sm;
		@include media-breakpoint-up(lg)  {
		margin-top: $spacer-lg;
	
	}
}

//img dividers
.imgDivider {
	position: relative;
	margin: 0;
	width: 100%;
	@include media-breakpoint-up(lg) {
		margin: $spacer-xl 0;
	}
	&__img {
		height: 25vh;
		overflow: hidden;
		position: relative;
		overflow: hidden;
		@include media-breakpoint-up(lg)  {
			height: 40vh;
		}
		> img {
			object-fit: cover;
			width: 100%;
		}
	}
	&.with-vid {
		position: relative;
	}
	&-text {
		position: absolute;
		height: 55px;
		bottom: -33px;
		right: 30px;
	}
}

//parallax
.imgDivider__img {
	position: relative;
	img {
		position: absolute;
		opacity: 1;
		height: 130%;
		top: -30%;
	}
}

$videoSize: 400px;
$videoBorder: calc(#{$videoSize} + 30px);
$videoSize-md: 350px;
$videoBorder-md: calc(#{$videoSize-md} + 30px);
$videoSize-sm: 280px;
$videoBorder-sm: calc(#{$videoSize-sm} + 30px);

.circle__vid__wrap {
	position: absolute;	
	top: 40%;
	right: 30px;
	border: 4px solid $oldpages;
	border-radius: 50%;
	padding: 10px;
	
		 @include media-breakpoint-down(sm)  {
			display: none;
			}
/*
	height: calc(#{$videoSize} + 30px);
	width: calc(#{$videoSize} + 30px);
*/
	 @include media-breakpoint-up(md)  {
		 height:$videoBorder-md;
		 width: $videoBorder-md;
		}
}

.circle__vid {
	z-index:2;
	overflow: hidden;
    border-radius: 50%;
		display: block;
		position: relative;
		@include media-breakpoint-up(md)  {
		 	height: $videoSize-md;
		 	width: $videoSize-md;
		 }
		 video {
			 height: 400px;
			 position: absolute;
			 top: 0;
			 left: 0;
			 right: 0;
			 margin: 0 auto;
			 transform: translateX(-24%) translateY(-5%);
		 }
	}
	
	
	// online icon
	.online-icon {
		padding-left: 25px;
		background: url('../images/online-icon.svg') no-repeat center left;
	}

.op0 {
	opacity: 0;
}

.spacer-up {
	padding : $spacer-sm 0;
		@include media-breakpoint-up(md) {
		padding : $spacer-xl 0;
		}
}
.lg__video__box {
			@include media-breakpoint-up(md) {
			width: 80%;
		}
}

.disabled {
	pointer-events: none;
}
.panel {
	padding: $spacer-sm;
	margin-top: $spacer-md;
}

.logo-block {
	@include media-breakpoint-down(lg) {
		max-width: 65%;
		margin: $spacer-sm auto;
	}
	a {
		display: block;
		img {
			transition: all 0.3s ease-in-out;
		}
		&:hover {
			cursor: pointer;
			img {
				opacity: 0.7;
			}
		}
	}
	&-fw {
		@include media-breakpoint-up(lg) {
		max-width: 35%;
		margin: $spacer-sm auto;
		}
	}
}

.iframe-container {
	position: relative;
	width: 100%;
	height: 80vh;
	iframe {
		height: 100%;
		width: 100%;
		z-index: 5;
	}
	.mapScreen {
		position: absolute;
		content: '';
		background-color: transparentize(white, 0.2);
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		height: 100%;
		width: 100%;
		z-index: 10;
		display: flex;
		justify-content: center;
		opacity: 0;
		align-items: center;
		transition: all 0.1s ease-in;
		&.initiate {
			pointer-events: none;
			opacity: 0!important;
			
		}
		p {
			font-size: 140%;
		}
		&:hover {
			opacity: 0.8;
			
		}
	}
}