#nav {
	//opacity: 0;
	//transform: translateY(-100%);
	background-color: transparentize($space, 0.2);
	border-bottom: 1px solid transparentize($oldpages, 0.7);
	width: 100%;
	position: fixed;
	padding: 0;
	.navbar-brand {
		img {
			height: 22px;
				@include media-breakpoint-up(sm) {
					height: 24px;
			}
		}
	}
	.navbar-nav {
		.nav-item {
			 	margin: 0 $spacer-xxs;
			.nav-link {
				font-size: 28px;
				padding: $spacer-xs ;
				font-family: $serif;
				font-weight: $reg;
				color: $oldpages;
				transition: all 0.3s ease-in-out;
				@include media-breakpoint-up(lg)  {
					font-size: 16px;
				}
				@include media-breakpoint-up(xl)  {
					font-size: 18px;
				}
				&:hover {
					color: $evergreen;
				}
				&::first-letter {
					color: $evergreen;
				}
			}
			&.language-switcher .nav-link {
				margin-left: $spacer-xs;
				margin-right: $spacer-xs;
				padding-left: 22px;
				background: url('../images/lang.svg') no-repeat center left;
				&::first-letter { color: $oldpages; } 
				&:hover {
					color: $oldpages;
				}
			}
						


			/*
&__sns {
				a { 
					padding: 0;
					display: inline-block;
					img {
						display: inline-block;
						width: auto;
						height: 29px;
					}
				}
			}
*/
		}
	}
	.navbar-toggler {
		position: relative;
		display: inline-block;
		width: 35px;
		padding-top: $spacer-xs;
		padding-bottom: $spacer-xs;
		transform-origin: center;
		transition: all 0.3s ease-in-out;
		display: block;
		@include media-breakpoint-up(lg)  {
			display: none;
		}
		span, span:after, span:before {
			height: 100%;
			width: 100%;
		}
		span {
			height: 2px;
			width: 100%;
			left: 0;
			right: 0;
			margin: 0 auto;
			display: block;
			background-color: transparent;
			position: absolute;
			&::after {
				content: '';
				position: absolute;
				top: -5px;
				right: 0;
				background-color: $oldpages;
			}
				&::before {
				content: '';
				position: absolute;
				top: 5px;
				right: 0;
				background-color: $evergreen;
			
			}
		}
		&.collapsed {
			transform: rotate(0deg);
			span:before {
				background-color: $oldpages;
			}
		}
		
		&.isOpen {
			transform: rotate(-90deg);
		}
	}
	&.isOpen {
		background-color: transparentize($space, 0.1);
	}
	
	
	//drowpdown
	@include media-breakpoint-up(md)  {
	.navbar-nav .dropdown > a.nav-link {
		position: relative;
		padding-right: 15px;
		&:after {
			content: '';
			position: absolute;
			height: 10px;
			width: 10px;
			right: 0;
			top: 24px;
			background: url('../images/arrow_down.svg');
			background-repeat: no-repeat;
			background-position: center center;
		}
		
	}
	}
	
	.dropdown:hover {
		> .dropdown-menu {
			opacity: 1!important;
			pointer-events: auto;
			
		}
	}	
	.dropdown-menu {
		transition: all 0.3s ease-in-out;
		pointer-events: none;
		opacity: 0;
		list-style-type: none;
		padding: $spacer-xxs;
		margin: 0;
		background-color: transparentize($space, 0.2);
		border: 1px solid transparentize($oldpages, 0.7);
		@include media-breakpoint-down(sm)  {
			display: none!important;
		}
		
		a {
			color: $oldpages;
			font-family: $serif;
			transition: all 0.3s ease-in-out;
			text-decoration: none;
			padding: $spacer-xxs;
			display: inline-block;
			&:hover {
				background-color: transparent;
				color: $evergreen;
			}
		}
	}	
		

	
}

#notice-bar {
	opacity: 0;
	z-index: 10;
	position: absolute;
	top: 70px;
	left: 0;
	right: 0;
	color: $space;
	@include media-breakpoint-up(md)  {
			top: 90px;
	}
	.notice-bar__text {
		padding: calc( #{$spacer-xs} / 2);
		border-radius: 5px;
		border: 1px solid #F84F31;
		text-align: center;
		@include media-breakpoint-up(md)  {
				padding: $spacer-xs;
			}
	}
	p {
		color: #F84F31;
		margin-bottom: 0;
		font-size: 13px;
			@include media-breakpoint-up(md)  {
				font-size: 16px;
			}
	}
	
}

.footer {
	padding: $spacer-sm 0;
	text-align: center;
	p {
		margin-bottom: 0;
	}
	.copyright {
		opacity: 0.7;
	}
}

// tab
.nav-tab {
	margin-bottom: $spacer-md;
	ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		li {
			display: inline-block;
			padding: $spacer-xs;
			font-size: $fs-sm;
			font-family: $sans;
			font-weight: $reg;
			position: relative;
			letter-spacing: 1px;
			@include media-breakpoint-up(lg)  {
				padding: $spacer-xxs $spacer-xs ;
			}
			&.active:after {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				right: 0;
				margin: 0 auto;
				width: 100%;
				height: 2px;
				background-color: $oldpages;
			}
			a {
				display: inline-block;
				text-decoration: none;
				color: $oldpages;
				opacity: 0.6;
				transition: all 0.3s ease-in-out;
				&:hover {
					opacity: 1;
				}
			}
		}
	}
}
.sec-bg-cream {
	.nav-tab ul li {
		&.active:after {
		background-color: $evergreen;
		}
		a {
			color: $evergreen;
		}
	}
}
// pagination 
.nav-pagination {
	border-top: 1px solid transparentize($oldpages, 0.7);
	margin-top: $spacer-sm;
	padding-top: $spacer-sm;
	text-align: center;
	font-family: $ja;
	.pagination {
		list-style-type: none;
		margin: 0;
		padding: 0;
		.page-item { 
			font-size: $fs-md;
			display: inline; 
			padding: $spacer-xxs $spacer-xs;
			.current {
				border-bottom: 2px solid $oldpages;
			}
			a {
				text-decoration: none;
				color: $oldpages;
				opacity: 0.5;
				transition: all 0.3s ease-in-out;
					&:hover {
						opacity: 1;
					}
				}
			}
	}
}
.sec-bg-cream {
	.nav-pagination {
		.pagination .page-item  {
			.current {
				border-color: $space;
			}
			a {
				color: $evergreen;
			}
		}
	}
}