// Colours

$evergreen:					#7F9777;
$space:							#151515;
$oldpages:					#BFBCB1;


// Spacers
$spacer-default: 8px;
$spacer-xxs: $spacer-default;
$spacer-xs: calc(#{$spacer-default} * 2); //16
$spacer-sm:  calc(#{$spacer-default} * 4); //32
$spacer-md:  calc(#{$spacer-default} * 6); //48
$spacer-lg:  calc(#{$spacer-default} * 9); //72
$spacer-xl:  calc(#{$spacer-default} * 11); //99
$spacer-xxl:  calc(#{$spacer-default} * 13); //104
$spacer-dl:  calc(#{$spacer-default} * 15); //104
$spacer-ddl:  calc(#{$spacer-default} * 17); //104



// keyframe animations
@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 20s linear infinite;
  -moz-animation: rotating 20s linear infinite;
  -ms-animation: rotating 20s linear infinite;
  -o-animation: rotating 20s linear infinite;
  animation: rotating 20s linear infinite;
}