.flex__vid {
  display: flex;
  justify-content: center;  
  flex-wrap: wrap; 
  align-content: flex-start;

	> div{
		  width: calc(100%/1);
	    padding: $spacer-xs;
			margin-bottom: $spacer-xs;
	    @include media-breakpoint-up(lg)  {
			 width: calc(100%/3);
			}
			@include media-breakpoint-up(xl)  {
			 width: calc(100%/4);
			}
	  }
	  &-thirds {
		  	> div{
			  width: calc(100%/1);
		    padding: $spacer-sm;
		    margin-bottom: 0;
		   
	
		    @include media-breakpoint-up(lg)  {
				 width: calc(100%/3);
				}
			
		  }
	  }
 }

