.btn-custom {
	display: inline-block;
	background-color: $evergreen;
	text-decoration: none;
	color: $space;
	font-family: $serif;
	font-size: $fs-sm;
	font-weight: $reg;
	text-align: center;
	padding: $spacer-xxs $spacer-md;
	transition: all 0.3s ease-in-out;
	border: 1px solid $evergreen;
	display: block;	
	&:hover {
		background-color: $space;
		color: $evergreen; 
	}
	&-sm {
		font-size: 18px;
		&:hover {
		
		}
	}
	@include media-breakpoint-up(lg)  {
	display: inline-block;
	}
	&.ghost {
		background-color: transparent;
		border: 1px solid $evergreen;
		color: $evergreen;
		&:hover {
			background-color: $evergreen;
			color: $space;
		}
		&-light {
			border-color: $oldpages;
			color: $oldpages;
		}
		&-dark {
			border-color: $space;
			color: $space;
			&:hover {
				background-color: $space;
				color: $oldpages;
			}
		}
	}
}


#nav {
	.btn-custom {
		margin-bottom: $spacer-md;
		padding: $spacer-xxs $spacer-xs;
		
		@include media-breakpoint-up(lg)  {
			
			margin-bottom: 0;
		}
		
		@include media-breakpoint-up(xl)  {
			padding: $spacer-xxs $spacer-sm;
		}
		
		
	}
}

