	font-size: 112.5%; /*18px*/
}
$ja: ibm-plex-sans-jp, sans-serif;
$ja2 : source-han-sans-japanese, sans-serif;
$serif: new-spirit, serif;
$sans: neue-haas-unica, sans-serif;

$reg:					400;
$med: 				500;
$bold: 				700;

$fs-xxs:    0.64rem;  /*11.52px	*/
$fs-xs:  		0.8rem;   /*14.40px	*/
$fs-sm:  		1rem;     /*18.00px	*/
$fs-md:    	1.25rem;  /*22.50px	*/
$fs-lg:     1.563rem; /*28.13px	*/
$fs-xl:     1.953rem; /*35.16px	*/
$fs-xxl:   	2.441rem; /*43.95px	*/
$fs-dl:   	3.052rem; /*54.93px	*/

