body.ja {
	font-family: $ja;
	font-weight: $reg;

}
.en-serif {
	font-family: $serif;
	font-weight: $reg;
}
.en-sans {
	font-family: $sans;
}
p {
	line-height: 170%;
}
.p-sm {
	font-size: $fs-xs;
}
.p-bold {
	font-weight: $bold;
}
.p-med {
	font-weight: $med;
}
.p-lg {
	font-size: $fs-xs;
	@include media-breakpoint-up(md)  {
		font-size: $fs-sm;
	}
}
.p-xs-c {
	@include media-breakpoint-down(sm)  {
		text-align: center;
	}
}
.p-bordered {
	border-top: 1px solid $space;
	border-bottom: 1px solid $space;
	margin: $spacer-sm 0;
	font-size: 110%;
	padding: $spacer-xxs 0;
}

.h1__title {
	font-size: $fs-lg;
	line-height: 1.4;
	span {
		font-size: 60%;
		display: block;
		@include media-breakpoint-up(lg)  {
		display: inline;
		}
	}
	@include media-breakpoint-up(lg)  {
	font-size: $fs-xxl;
	}
	
}
.h2__title {
	font-size: $fs-xxl;
	margin-bottom: $spacer-sm;
	@include media-breakpoint-up(lg)  {
	
	}
}
.h3__title {
	font-size: $fs-lg;
	font-weight: $reg;
	margin-bottom: $spacer-sm;
	@include media-breakpoint-up(lg)  {
		font-size: $fs-xl;
	}
}
.h4__title {
	font-size: $fs-md;
	margin-bottom: $spacer-xs;
	@include media-breakpoint-up(lg)  {
	font-size: $fs-lg;
	}
}
.h5__title {
	font-size: $fs-md;
	@include media-breakpoint-up(lg)  {
	
	}
}
.h6__title {
	font-size: $fs-sm;
	font-weight: $bold;
	@include media-breakpoint-up(lg)  {
		font-size: $fs-sm;
	}
}

.link-text {
	position: relative;
	color: $evergreen;
	display: inline-block;
	text-decoration: none;
	&:after {
		z-index: -1;
		position: absolute;
		content: '';
		width: 100%;
		height: 0;
		bottom: 0;
		left: 0;
		margin: 0 auto;
		background-color: $evergreen;
		transition: all 0.1s ease-in-out;
	}
	&:hover {
		cursor: pointer;
		color: $evergreen;
		&:after {
			height: 1px;
		}
	}
}

.page__title {
	margin-top: $spacer-sm;
	padding: $spacer-sm 0;
	border-bottom: 1px solid transparentize($oldpages, 0.7);
	@include media-breakpoint-up(lg)  {
		margin-top: $spacer-lg;
		padding: $spacer-lg 0;
	}
	&-center {
		text-align: center;
	}
	&-light {
		border-color: $evergreen;
	}
}