.wec {
	padding: $spacer-xs;
	background-color: $oldpages;

	@include media-breakpoint-down(lg)  {
		width: 50%;
		margin: 0 auto;
		margin-bottom: $spacer-sm;
	}
	
}

.card__media {
	margin-bottom: $spacer-xs;
	display: flex;
	&__img {
		width: $spacer-lg;
		margin-right: $spacer-xs;
		img {
			object-fit: contain;
			border-radius: 50%;
		}
	}
	&__text {
		h5 {
			font-family: $serif;
		}
	}
}

.video__box {
	position: relative;
	width: 100%;
	height: auto;
	overflow: hidden;
	padding-bottom: 0;

	.video__box__text {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		height: 100%;
		width: inherit;
		display: flex;
		justify-content: center;
		align-items: center;
		p {
			font-family: $serif;
			font-size: 200%;
			font-weight: $reg;
			color: $evergreen;
		}
	}
	
	.video__box {
		position: relative;
		display: inline-block;
		transition: all 0.5s ease-in-out;
		width: 100%;
		background-color: $space;
		text-align: center;
		 > a {
			 text-align: center;
		 }
		.video__box__img {
			transition: opacity 0.5s ease-in-out;
			opacity: 0.3;
			@include media-breakpoint-up(md) {
				opacity: 0.8;
			}
		}
		.icon_play {
			opacity: 1;
			height: 80px;
			width: 80px;
			transition: all 0.3s ease-in-out;
			@include media-breakpoint-up(md) {
				height: 120px;
				width: 120px;
			}
			&-sm {
				height: 50px;
				width: 50px;
				@include media-breakpoint-up(md) {
					height: 70px;
					width: 70px;
				}
			}
		}
	
	}
	.video__box__title {
			font-weight: $bold;
			line-height: 1.6;
			margin-bottom: 0;
		}
}

.video__box {
	.video__box:hover {
		cursor: pointer;
		transform: scale(1.05);
		z-index: 5;
			.video__box__img {
			opacity: 0.3;
			}
			.icon_play {
				opacity: 1;
			}
	}
}

.card__post {
	a {
		display: inline-block;
		background-color: white;
		text-decoration: none;
		color: $black;
		transition: all 0.3s ease-in-out;
		transform: scale(1);
		&:hover {
			transform: scale(1.02);
			color: $evergreen;
			box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
		}
	}
	&__img {
		overflow: hidden;
		height: 190px;
		overflow: hidden;
		img {
			object-fit: cover;
			height: 100%;
			width: 100%;
		}
	}
	&__content {
		padding: $spacer-xs;
		p {
			margin-bottom: 0;
		}
		&-cat {			
			font-weight: bold;
		}
	}
	.card__post__content-details {
		margin-bottom: 5px;
		font-size: 80%;
		color: $evergreen;
		display: flex;
		justify-content: space-between;
	}
}